ion() {\r\n      if (this.currentPage && this.page) {\r\n        return this.currentPage >= this.page;\r\n      }\r\n      return false;\r\n    }\r\n  },\r\n  created() {\r\n    this.init();\r\n  },\r\n  methods: {\r\n    dateFtt,\r\n    init() {\r\n      var self = this;\r\n      window.addEventListener(\r\n        \"scroll\",\r\n        _.throttle(function() {\r\n          // console.log(\"底部必读\");\r\n          if (\r\n            window.pageYOffset + window.innerHeight >=\r\n            document.documentElement.scrollHeight\r\n          ) {\r\n            console.log(\"底部必读\");\r\n            self.scroll_bottom();\r\n          }\r\n        }, 200),\r\n        false\r\n      );\r\n    },\r\n    scroll_bottom() {\r\n      var self = this;\r\n      this.getList(self.currentPage + 1);\r\n    },\r\n    getList(currentPage) {\r\n      var self = this;\r\n      get_index_list({\r\n        data: {\r\n          currentPage\r\n        },\r\n        success(result) {\r\n          var jsonResult = JSON.parse(result);\r\n          if (jsonResult[\"result\"].length > 0) {\r\n            self.list.concat(jsonResult[\"result\"]);\r\n            self.changeResultAndPage(self.list, JSON.parse(result)[\"page\"]);\r\n          } else {\r\n            layer.msg(\"这回真的没有了\");\r\n          }\r\n        }\r\n      });\r\n    },\r\n    pageItemClick(currentPage) {\r\n      var self = this;\r\n      this.changePage(currentPage);\r\n      this.$nextTick(() => {\r\n        self.getList(self.currentPage);\r\n      });\r\n    }\r\n  }\r\n};\r\n</script>"],"sourceRoot":""}]);

